import React,{useEffect} from 'react'
import { Link } from 'react-router-dom';
import Header from "../components/Header"
import Footer from "./Footer"

function PrivacyPolicy() {

    useEffect(()=>{
        document.documentElement.scrollTop = 0;
    })

    return (
        <React.Fragment>
            <Header/>
            <div className="policy-p">
                <div className="wrapper flex flex-col">
                    <div className="title font s26 b7 color">Privacy Policy</div>
                    <div className="sub font s14 c333">Last updated 1st March 2020</div>
                    <div className="block">
                        <div className="txt font s15 c000">Thank you for choosing to be part of our community at localpsyche.com or Local Psyche (<b>Company</b>, <b>we</b>, <b>us</b>, or <b>our</b>). We are committed to protecting your personal information and your right to privacy. If you have any questions or concerns about our policy, or our practices with regards to your personal information, please contact us at <span className="color">cs@localpsyche.com</span> When you visit our website <a target="_blank" className="color lin" href="https://localpsyche.com/mobile">https://localpsyche.com/mobile</a> application, and use our services, you trust us with your personal information. We take your privacy very seriously. In this privacy policy, we seek to explain to you in the clearest way possible what information we collect, how we use it and what rights you have in relation to it. We hope you take some time to read through it carefully, as it is important. If there are any terms in this privacy policy that you do not agree with, please discontinue use of our Sites or Apps and our services. This privacy policy applies to all information collected through our website (such as <a target="_blank" href="https://localpsyche.com" className="lin color">https://localpsyche.com</a>), mobile application, (<b>Apps</b>), and/or any related services, sales, marketing or events (we refer to them collectively in this privacy policy as the Services).</div>
                        <div className="not txt font s15 b c000">Please read this privacy policy carefully as it will help you make informed decisions about sharing your personal information with us.</div>
                    </div>
                    <div className="block"> 
                        <div className="lbl font s16 b7 c000">1. WHAT INFORMATION DO WE COLLECT?</div>
                        
                        <div className="marg-t font s16 b c000">Personal information you disclose to us</div>
                        <div className="txt font s15 c000"><span className="b6">In Short:</span>&nbsp;We collect personal information that you provide to us. We collect personal information that you voluntarily provide to us when registering at the Services or Apps, expressing an interest in obtaining information about us or our products and services, when participating in activities on the Services or Apps (such as posting messages in our online forums or entering competitions, contests or giveaways) or otherwise contacting us. The personal information that we collect depends on the context of your interactions with us and the Services or Apps, the choices you make and the products and features you use. The personal information we collect can include the following:</div>
                        <div className="txt font s15 c000"><span className="b6">Publicly Available Personal Information.</span>&nbsp;We collect first name, maiden name, last name, and nickname; ID; drivers license number; passport number; current and former address; phone numbers; email addresses; criminal records and mugshots; government licenses, professional licenses, hunting/fishing permits, and weapons permits; social media; and other similar data.</div>
                        <div className="txt font s15 c000"><span className="b6">Personal Information Provided by You.</span>&nbsp;We collect data about health, DNA, medical records, FitBit, and similar apps; app usage; political and social affiliation to different groups; data collected from surveys; CV and other job application data such as background checks; work performance, salary, bonuses, and disciplinary actions; purchase history; financial information (credit card number, purchase history, invoices); passwords; and other similar data.</div>
                        <div className="txt font s15 c000"><span className="b6">Payment Data.</span>&nbsp;We may or may not collect data necessary to process your payment if you make purchases, such as your payment instrument number (such as a credit card number), and the security code associated with your payment instrument.</div>
                        <div className="txt font s15 c000"><span className="b6">Social Media Login Data.</span>&nbsp;We may provide you with the option to register using social media account details, like your Facebook, Twitter or other social media account. If you choose to register in this way, we will collect the Information described in the section called <span className="color">HOW DO WE HANDLE YOUR SOCIAL LOGINS</span> below. All personal information that you provide to us must be true, complete and accurate, and you must notify us of any changes to such personal information.</div>

                        <div className="marg-t font s16 b c000">Information automatically collected</div>
                        <div className="txt font s15 c000"><span className="b6">In Short:</span>&nbsp;Some information  such as IP address and/or browser and device characteristics  is collected automatically when you visit our Services or Apps. We automatically collect certain information when you visit, use or navigate the Services or Apps. This information does not reveal your specific identity (like your name or contact information) but may include device and usage information, such as your IP address, browser and device characteristics, operating system, language preferences, referring URLs, device name, country, location, information about how and when you use our Services or Apps and other technical information. This information is primarily needed to maintain the security and operation of our Services or Apps, and for our internal analytics and reporting purposes.<br/>Like many businesses, we also collect information through cookies and similar technologies.</div>
                        <div className="txt font s15 c000"><span className="b6">Online Identifiers.</span>&nbsp;We collect devices; applications; cookie identifiers, or others such as the ones used for analytics and marketing; Radio Frequency Identification (RFID) tags; devices geolocation; and other similar data.</div>
                    
                        <div className='marg-t font s16 b c000'>Information collected through our Apps</div> 
                        <div className="txt font s15 c000"><span className="b6">In Short:</span>&nbsp;We may collect information regarding your geo-location, mobile device, when you use our apps.</div>
                        <div className="m10">If you use our Apps, we may also collect the following information:</div>
                        <ul className='list'>
                            <li className="txt font s15 c000">Geo-Location Information. We may request access or permission to and track location-based information from your mobile device, either continuously or while you are using our mobile application, to provide location-based services. If you wish to change our access or permissions, you may do so in your devices settings.</li>
                            <li className="txt font s15 c000">Mobile Device Access. We may request access or permission to certain features from your mobile device, including your mobile devices bluetooth, calendar, camera, microphone, contacts, reminders, sensors, social media accounts, sms messages, storage, data, and other features. If you wish to change our access or permissions, you may do so in your devices settings.</li>
                            <li className="txt font s15 c000">Mobile Device Data. We may automatically collect device information (such as your mobile device ID, model and manufacturer), operating system, version information and IP address.</li>
                        </ul>
                        <div className="marg-t font s16 b c000">Information collected from other sources</div>
                        <div className="txt font s15 c000"><span className="b6">In Short:</span>&nbsp;We may collect limited data from public databases, marketing partners, social media platforms, and other outside sources. We may obtain information about your from other sources, such as public databases, joint marketing partners, social media platforms (such as Facebook), as well as from other third parties. Examples of the information we receive from other sources include: social media profile information (your name, gender, birthday, email, current city, state and country, user identification numbers for your contacts, profile picture URL, and any other information that you choose to make public); marketing leads and search results and links, including paid listings (such as sponsored links). We will inform you about the source of information and the type of information and the type of information we have collected about you within a reasonable period after obtaining the personal data, but at the latest within one month.</div>
                    </div>

                    <div className="block"> 
                        <div className="lbl font s16 b7 c000">2. HOW DO WE USE YOUR INFORMATION?</div>
                        <div className="txt font s15 c000"><span className="b6">In Short:</span>&nbsp; We process your information for purposes based on legitimate business interests, the fulfillment of our contract with you, compliance with our legal obligations, and/or your consent. We use personal information collected via our Services or Apps for a variety of business purposes described below. We process your personal information for these purposes in reliance on our legitimate business interests, in order to enter into or perform a contract with you, with your consent, and/or for compliance with our legal obligations. We indicate the specific processing grounds we rely on next to each purpose listed below.</div>
                        <div className="m10 txt font s15 c000">We use the information we collect or receive:</div>
                        <ul className='list'>
                            <li className="txt font s15 c000"><span className="b7">To facilitate account creation and logon process.</span> If you choose to link your account with us to a third party account (such as your Google or Facebook account), we use the information you allowed us to collect from those third parties to facilitate account creation and logon process for the performance of the contract. See the section below headed HOW DO WE HANDLE YOUR SOCIAL LOGINS for further information.</li>
                            <li className="txt font s15 c000"><span className="b7">To send administrative information to you.</span> We may use your personal information to send you product, service and new feature information and/or information about changes to our terms, conditions, and policies.</li>
                            <li className="txt font s15 c000"><span className="b7">To protect our Services.</span> We may use your information as part of our efforts to keep our Services or Apps safe and secure (for example, for fraud monitoring and prevention).</li>
                            <li className="txt font s15 c000"><span className="b7">To enable user-to-user communications.</span> We may use your information in order to enable user-to-user communications with each users consent.</li>
                            <li className="txt font s15 c000"><span className="b7">To enforce our terms, conditions and policies for Business Purposes, Legal Reasons and Contractual.</span></li>
                            <li className="txt font s15 c000"><span className="b7">To respond to legal requests and prevent harm.</span> If we receive a subpoena or other legal request, we may need to inspect the data we hold to determine how to respond.</li>
                            <li className="txt font s15 c000"><span className="b7">To manage user accounts.</span> We may use your information for the purposes of managing our account and keeping it in working order.</li>
                            <li className="txt font s15 c000"><span className="b7">To deliver services to the user.</span> We may use your information to provide you with the requested service.</li>
                        </ul>
                    </div> 

                    <div className="block"> 
                        <div className="lbl font s16 b7 c000">3. WILL YOUR INFORMATION BE SHARED WITH ANYONE?</div>
                        <div className="txt font s15 c000"><span className="b6">In Short:</span>&nbsp;We only share information with your consent, to comply with laws, to provide you with services, to protect your rights, or to fulfill business obligations. We may process or share data based on the following legal basis:</div>
                        <ul className='list'>
                            <li className="txt font s15 c000"><span className="b7">Consent:</span> We may process your data if you have given us specific consent to use your personal information in a specific purpose.</li>
                            <li className="txt font s15 c000"><span className="b7">Legitimate Interests:</span> We may process your data when it is reasonably necessary to achieve our legitimate business interests.</li>
                            <li className="txt font s15 c000"><span className="b7">Performance of a Contract:</span> Where we have entered into a contract with you, we may process your personal information to fulfill the terms of our contract.</li>
                            <li className="txt font s15 c000"><span className="b7">Legal Obligations:</span> We may disclose your information where we are legally required to do so in order to comply with applicable law, governmental requests, a judicial proceeding, court order, or legal process, such as in response to a court order or a subpoena (including in response to public authorities to meet national security or law enforcement requirements).</li>
                            <li className="txt font s15 c000"><span className="b7">Vital Interests:</span>  We may disclose your information where we believe it is necessary to investigate, prevent, or take action regarding potential violations of our policies, suspected fraud, situations involving potential threats to the safety of any person and illegal activities, or as evidence in litigation in which we are involved. More specifically, we may need to process your data or share your personal information in the following situations:</li>
                            <li className="txt font s15 c000"><span className="b7">Vendors, Consultants and Other Third-Party Service Providers.</span> We may share your data with third party vendors, service providers, contractors or agents who perform services for us or on our behalf and require access to such information to do that work. Examples include: payment processing, data analysis, email delivery, hosting services, customer service and marketing efforts. We may allow selected third parties to use tracking technology on the Services or Apps, which will enable them to collect data about how you interact with the Services or Apps over time. This information may be used to, among other things, analyze and track data, determine the popularity of certain content and better understand online activity. Unless described in this Policy, we do not share, sell, rent or trade any of your information with third parties for their promotional purposes. We have contracts in place with our data processors. This means that they cannot do anything with your personal information unless we have instructed them to do it. They will not share your personal information with any organisation apart from us. They will hold it securely and retain it for the period we instruct.</li>
                            <li className="txt font s15 c000"><span className="b7">Business Transfers.</span> We may share or transfer your information in connection with, or during negotiations of, any merger, sale of company assets, financing, or acquisition of all or a portion of our business to another company.</li>
                            <li className="txt font s15 c000"><span className="b7">Third-Party Advertisers.</span> We may use third-party advertising companies to serve ads when you visit the Services or Apps. These companies may use information about your visits to our Website(s) and other websites that are contained in web cookies and other tracking technologies in order to provide advertisements about goods and services of interest to you.</li>
                            <li className="txt font s15 c000"><span className="b7">Other Users.</span> When you share personal information or otherwise interact with public areas of the Services or Apps, such personal information may be viewed by all users and may be publicly distributed outside the Services or Apps in perpetuity. If you interact with other users of our Services or Apps and register through a social network (such as Facebook), your contacts on the social network will see your name, profile photo, and descriptions of your activity. Similarly, other users will be able to view descriptions of your activity, communicate with you within our Services or Apps, and view your profile.</li>
                        </ul>
                    </div>

                    <div className="block"> 
                        <div className="lbl font s16 b7 c000">4. WHO WILL YOUR INFORMATION BE SHARED WITH?</div>
                        <div className="txt font s15 c000"><span className="b6">In Short:</span>&nbsp;We only share information with the following third parties. We only share and disclose your information with the following third parties. We have categorized each party so that you may be easily understand the purpose of our data collection and processing practices. If we have processed your data based on your consent and you wish to revoke your consent, please contact us.</div>
                        <ul className='list'>
                            <li className="txt font s15 c000"><span className="b7">Advertising, Direct Marketing, and Lead Generation</span><br/>Google AdSense</li>
                            <li className="txt font s15 c000"><span className="b7">Allow Users to Connect to their Third-Party Accounts</span><br/>Google account</li>
                            <li className="txt font s15 c000"><span className="b7">Cloud Computing Services</span><br/>Amazon Web Services (AWS)</li>
                            <li className="txt font s15 c000"><span className="b7">Communicate and Chat with Users</span><br/>chat</li>
                            <li className="txt font s15 c000"><span className="b7">Data Backup and Security</span><br/>Amazon Glacier</li>
                            <li className="txt font s15 c000"><span className="b7">Web and Mobile Analytics</span><br/>Facebook Analytics, Google Analytics, Google Analytics for Firebase and Google Tag Manager</li>
                            <li className="txt font s15 c000"><span className="b7">Website Hosting</span><br/>WordPress.com</li>
                            <li className="txt font s15 c000"><span className="b7">Website Performance Monitoring</span><br/>Firebase Crash Reporting and Firebase Performance Monitoring</li>
                            <li className="txt font s15 c000"><span className="b7">Website Testing</span><br/>Firebase Remote Config and Google Play Console</li>
                        </ul>
                    </div> 

                    <div className="block"> 
                        <div className="lbl font s16 b7 c000">5. DO WE USE COOKIES AND OTHER TRACKING TECHNOLOGIES?</div>
                        <div className="txt font s15 c000"><span className="b6">In Short:</span>&nbsp;We may use cookies and other tracking technologies to collect and store your information. We may use cookies and similar tracking technologies (like web beacons and pixels) to access or store information. Specific information about how we use such technologies and how you can refuse certain cookies is set out in our Cookie Policy.</div>
                    </div>

                    <div className="block"> 
                        <div className="lbl font s16 b7 c000">6. DO WE USE GOOGLE MAPS?</div>
                        <div className="txt font s15 c000"><span className="b6">In Short:</span>&nbsp;Yes, we use Google Maps for the purpose of providing better service. This website, mobile application, or Facebook application uses Google Maps APIs. You may find the Google Maps APIs Terms of Service <a target="_blank" href="https://cloud.google.com/maps-platform/terms/" className="color">here.</a> To better understand Googles Privacy Policy, please refer to this <a target="_blank" href="https://policies.google.com/privacy" className="color">link.</a> By using our Maps API Implementation, you agree to be bound by Googles Terms of Service. By using our implementation of the Google Maps APIs, you agree to allow us to gain access to information about you including personally identifiable information (such as usernames) and non-personally identifiable information (such as location). We will be collecting the following information:</div>
                        <div className="txt font s15 c000">For a full list of what we use information for, please see the previous section titled HOW DO WE USE YOUR INFORMATION? and WILL YOUR INFORMATION BE SHARED WITH ANYONE? You agree to allow us to obtain or cache your location. You may revoke your consent at anytime. We use information about location in conjunction with data from other data providers. The Maps APIs that we use store and access cookies and other information on your devices. If you are a user currently in the European Economic Area (EU countries, Iceland, Liechtenstein and Norway), please take a look at our EU User Consent Policy.</div>
                    </div>

                    <div className="block"> 
                        <div className="lbl font s16 b7 c000">7. HOW DO WE HANDLE YOUR SOCIAL LOGINS?</div> 
                        <div className="txt font s15 c000"><span className="b6">In Short:</span>&nbsp; If you choose to register or log in to our services using a social media account, we may have access to certain information about you. Our Services or Apps offer you the ability to register and login using your third party social media account details (like your Facebook or Twitter logins). Where you choose to do this, we will receive certain profile information about you from your social media provider. The profile Information we receive may vary depending on the social media provider concerned, but will often include your name, e-mail address, friends list, profile picture as well as other information you choose to make public.</div>
                        <div className="txt font s15 c000">We will use the information we receive only for the purposes that are described in this privacy policy or that are otherwise made clear to you on the Services or Apps. Please note that we do not control, and are not responsible for, other uses of your personal information by your third party social media provider. We recommend that you review their privacy policy to understand how they collect, use and share your personal information, and how you can set your privacy preferences on their sites and apps.</div>
                    </div>

                    <div className="block"> 
                        <div className="lbl font s16 b7 c000">8. IS YOUR INFORMATION TRANSFERRED INTERNATIONALLY?</div> 
                        <div className="txt font s15 c000"><span className="b6">In Short:</span>&nbsp;We may transfer, store, and process your information in countries other than your own.</div>
                        <div className="txt font s15 c000">Our servers are located in United Kingdom, and United States. If you are accessing our Services or Apps from outside United Kingdom, and United States, please be aware that your information may be transferred to, stored, and processed by us in our facilities and by those third parties with whom we may share your personal information (see WILL YOUR INFORMATION BE SHARED WITH ANYONE? above), in and other countries.</div>
                        <div className="txt font s15 c000">If you are a resident in the European Economic Area, then these countries may not have data protection or other laws as comprehensive as those in your country. We will however take all necessary measures to protect your personal information in accordance with this privacy policy and applicable law.</div>
                    </div>

                    <div className="block"> 
                        <div className="lbl font s16 b7 c000">9. WHAT IS OUR STANCE ON THIRD-PARTY WEBSITES?</div> 
                        <div className="txt font s15 c000"><span className="b6">In Short:</span>&nbsp;We are not responsible for the safety of any information that you share with third-party providers who advertise, but are not affiliated with, our websites. The Services or Apps may contain advertisements from third parties that are not affiliated with us and which may link to other websites, online services or mobile applications. We cannot guarantee the safety and privacy of data you provide to any third parties. Any data collected by third parties is not covered by this privacy policy. We are not responsible for the content or privacy and security practices and policies of any third parties, including other websites, services or applications that may be linked to or from the Services or Apps. You should review the policies of such third parties and contact them directly to respond to your questions.</div>
                    </div>

                    <div className="block"> 
                        <div className="lbl font s16 b7 c000">10. HOW LONG DO WE KEEP YOUR INFORMATION?</div> 
                        <div className="txt font s15 c000"><span className="b6">In Short:</span>&nbsp;We keep your information for as long as necessary to fulfill the purposes outlined in this privacy policy unless otherwise required by law. We will only keep your personal information for as long as it is necessary for the purposes set out in this privacy policy, unless a longer retention period is required or permitted by law (such as tax, accounting or other legal requirements). No purpose in this policy will require us keeping your personal information for longer than the period of time in which users have an account with us. When we have no ongoing legitimate business need to process your personal information, we will either delete or anonymize it, or, if this is not possible (for example, because your personal information has been stored in backup archives), then we will securely store your personal information and isolate it from any further processing until deletion is possible.</div>
                    </div>

                    <div className="block"> 
                        <div className="lbl font s16 b7 c000">11. HOW DO WE KEEP YOUR INFORMATION SAFE?</div> 
                        <div className="txt font s15 c000"><span className="b6">In Short:</span>&nbsp;We aim to protect your personal information through a system of organizational and technical security measures. We have implemented appropriate technical and organizational security measures designed to protect the security of any personal information we process. However, please also remember that we cannot guarantee that the internet itself is 100% secure. Although we will do our best to protect your personal information, transmission of personal information to and from our Services or Apps is at your own risk. You should only access the services within a secure environment.</div>
                    </div>

                    <div className="block"> 
                        <div className="lbl font s16 b7 c000">12. WHAT ARE YOUR PRIVACY RIGHTS?</div> 
                        <div className="txt font s15 c000"><span className="b6">In Short:</span>&nbsp;In some regions, such as the European Economic Area, you have rights that allow you greater access to and control over your personal information. You may review, change, or terminate your account at any time. In some regions (like the European Economic Area), you have certain rights under applicable data protection laws. These may include the right (i) to request access and obtain a copy of your personal information, (ii) to request rectification or erasure; (iii) to restrict the processing of your personal information; and (iv) if applicable, to data portability. In certain circumstances, you may also have the right to object to the processing of your personal information. To make such a request, please use the contact details provided below. We will consider and act upon any request in accordance with applicable data protection laws.</div>
                        <div className="txt font s15 c000">If we are relying on your consent to process your personal information, you have the right to withdraw your consent at any time. Please note however that this will not affect the lawfulness of the processing before its withdrawal. If you are resident in the European Economic Area and you believe we are unlawfully processing your personal information, you also have the right to complain to your local data protection supervisory authority. You can find their contact details here: <a target="_blank" href="http://ec.europa.eu/justice/data-protection/bodies/authorities/index_en.htm." className="color">http://ec.europa.eu/justice/data-protection/bodies/authorities/index_en.htm.</a> If you have questions or comments about your privacy rights, you may email us at <span className='color'>cs@localpsyche.com</span></div>
                        <div className="txt font s15 c000"><span className="b7">Account Information</span><br/>If you would at any time like to review or change the information in your account or terminate your account, you can:</div>
                        <ul className='list'>
                            <li className="txt font s15 c000">Log into your account settings and update your user account.</li>
                            <li className="txt font s15 c000">Contact us using the contact information provided.</li>
                        </ul>
                        <div className="txt font s15 c000">Upon your request to terminate your account, we will deactivate or delete your account and information from our active databases. However, some information may be retained in our files to prevent fraud, troubleshoot problems, assist with any investigations, enforce our Terms of Use and/or comply with legal requirements.</div>
                        <div className="txt font s15 c000"><span className="b7">Cookies and similar technologies:</span>&nbsp;Most Web browsers are set to accept cookies by default. If you prefer, you can usually choose to set your browser to remove cookies and to reject cookies. If you choose to remove cookies or reject cookies, this could affect certain features or services of our Services or Apps. To opt-out of interest-based advertising by advertisers on our Services or Apps visit <a target="_blank" href="" className="color">http://www.aboutads.info/choices/.</a></div>
                        <div className="txt font s15 c000"><span className="b7">Opting out of email marketing:</span>&nbsp;You can unsubscribe from our marketing email list at any time by clicking on the unsubscribe link in the emails that we send or by contacting us using the details provided below. You will then be removed from the marketing email list  however, we will still need to send you service-related emails that are necessary for the administration and use of your account. To otherwise opt-out, you may:</div> 
                        <ul className='list'>
                            <li className="txt font s15 c000">Note your preferences when you register an account with the site.</li>
                            <li className="txt font s15 c000">Access your account settings and update preferences.</li>
                            <li className="txt font s15 c000">Contact us using the contact information provided.</li>
                        </ul>
                    </div>

                    <div className="block"> 
                        <div className="lbl font s16 b7 c000">13. DATA BREACH</div> 
                        <div className="txt font s15 c000">A privacy breach occurs when there is unauthorized access to or collection, use, disclosure or disposal of personal information. You will be notified about data breaches when Local Psyche believes you are likely to be at risk or serious harm. For example, a data breach may be likely to result in serious financial harm or harm to your mental or physical well-being. In the event that Local Psyche becomes aware of a security breach which has resulted or may result in unauthorized access, use or disclosure of personal information Local Psyche will promptly investigate the matter and notify the applicable Supervisory Authority not later than 72 hours after having become aware of it, unless the personal data breach is unlikely to result in a risk to the rights and freedoms of natural persons.</div>
                    </div>

                    <div className="block"> 
                        <div className="lbl font s16 b7 c000">14. CONTROLS FOR DO-NOT-TRACK FEATURES</div> 
                        <div className="txt font s15 c000">Most web browsers and some mobile operating systems and mobile applications include a Do-Not-Track (DNT) feature or setting you can activate to signal your privacy preference not to have data about your online browsing activities monitored and collected. No uniform technology standard for recognizing and implementing DNT signals has been finalized. As such, we do not currently respond to DNT browser signals or any other mechanism that automatically communicates your choice not to be tracked online. If a standard for online tracking is adopted that we must follow in the future, we will inform you about that practice in a revised version of this privacy policy.</div>
                    </div>

                    <div className="block"> 
                        <div className="lbl font s16 b7 c000">15. DO CALIFORNIA RESIDENTS HAVE SPECIFIC PRIVACY RIGHTS?</div> 
                        <div className="txt font s15 c000"><span className="b6">In Short:</span>&nbsp;Yes, if you are a resident of California, you are granted specific rights regarding access to your personal information. California Civil Code Section 1798.83, also known as the Shine The Light law, permits our users who are California residents to request and obtain from us, once a year and free of charge, information about categories of personal information (if any) we disclosed to third parties for direct marketing purposes and the names and addresses of all third parties with which we shared personal information in the immediately preceding calendar year. If you are a California resident and would like to make such a request, please submit your request in writing to us using the contact information provided below. If you are under 18 years of age, reside in California, and have a registered account with the Services or Apps, you have the right to request removal of unwanted data that you publicly post on the Services or Apps. To request removal of such data, please contact us using the contact information provided below, and include the email address associated with your account and a statement that you reside in California. We will make sure the data is not publicly displayed on the Services or Apps, but please be aware that the data may not be completely or comprehensively removed from our systems.</div>
                    </div>

                    <div className="block"> 
                        <div className="lbl font s16 b7 c000">16. DO WE MAKE UPDATES TO THIS POLICY?</div> 
                        <div className="txt font s15 c000"><span className="b6">In Short:</span>&nbsp;Yes, we will update this policy as necessary to stay compliant with relevant laws. We may update this privacy policy from time to time. The updated version will be indicated by an updated Revised date and the updated version will be effective as soon as it is accessible. If we make material changes to this privacy policy, we may notify you either by prominently posting a notice of such changes or by directly sending you a notification. We encourage you to review this privacy policy frequently to be informed of how we are protecting your information.</div>
                    </div>

                    <div className="block"> 
                        <div className="lbl font s16 b7 c000">17. HOW CAN YOU CONTACT US ABOUT THIS POLICY?</div> 
                        <div className="txt font s15 c000">If you have questions or comments about this policy, you may email us at <span className="color">cs@localpsyche.com</span></div>
                        <div className="txt font s15 c000"><span className="b7">HOW CAN YOU REVIEW, UPDATE, OR DELETE THE DATA WE COLLECT FROM YOU?</span>&nbsp;Based on the laws of some countries, you may have the right to request access to the personal information we collect from you, change that information, or delete it in some circumstances. To request to review, update, or delete your personal information, please email: <span className="color">cs@localpsyche.com</span> . We will respond to your request within 30 days.</div>
                    </div>
                </div>
            </div>
            <Footer/> 
        </React.Fragment>
    );
}

export default PrivacyPolicy;